@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#loading {
  text-align: center;
}

.icon {
  font-size: 3rem;
  margin-left: 1rem;
}

.name {
  position: relative;
  top: -0.7rem;
  margin-left: 1rem;
}

.hab {
  margin-bottom: 0.5rem;
}
.hab-inside {
  padding: 0.5rem;
}

.mark {
  float: right;
  margin-top: 1rem;
  margin-right: 1rem;
}
.nomarg {
  margin-top: 0;
}
.noright {
  margin-right: 0;
}

.markLengthened {
  float: right;
  margin-top: 1rem;
  margin-right: 1rem;
  padding-left: 2rem;
  padding-right: 2rem;
}
.createButton {
  margin: 0;
  position: absolute;
  top: 45%;
  right: 7%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding-left: 4rem;
  padding-right: 4rem;
}
.backButton {
  margin: 0;
  position: absolute;
  top: 58%;
  right: 7%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding-left: 4.3rem;
  padding-right: 4.3rem;
}

.frequencyForm {
  margin: 0;
  position: absolute;
  top: 56%;
  right: 30%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding-left: 4.3rem;
  padding-right: 4.3rem;
  width: 15rem;
}

.activityForm {
  margin: 0;
  position: absolute;
  top: 56%;
  left: 5%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  padding-left: 4.3rem;
  padding-right: 4.3rem;
  width: 17rem;
}

#dropdown-basic {
  margin: 0;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  margin-left: 1.3rem;
}

#listGroup {
  max-width: 15rem;
}

#listGroupFormatted {
  width: 15rem;
  margin: 0;
  position: absolute;
  top: 50%;
  right: 40%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

nav {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(9, 9, 121, 1) 35%,
    rgba(0, 212, 255, 1) 100%
  );
}

.navbar-brand {
  font-family: "Pacifico", cursive;
}

username {
  color: white;
  font-weight: bold;
}
hr {
  border: 0;
  height: 1px;
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0.75),
    rgba(0, 0, 0, 0)
  );
  margin-top: 0;
}

#customers {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

#customers td,
#customers th {
  border: 1px solid #ddd;
  padding: 8px;
}

#customers tr:nth-child(even) {
  background-color: #f2f2f2;
}

#customers tr:hover {
  background-color: #ddd;
}

#customers th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #4caf50;
  color: white;
}
.cal-icon {
  font-size: 1.5rem;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.dashboard {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
-webkit-justify-content: center;
        justify-content: center;
}

.clickText {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

ol.breadcrumb {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
}

.buttonHover:hover {
  color:blue;
}
